.QuestionGroup {
  margin-top: 48px;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.15);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.15);
  border-radius: 4px;
}

.QuestionGroup-icon {
  padding-right: 14px;
}

.QuestionGroup-title {
  font-size: 24px;
  background-color: #111d4a;
  color: #f5f5f5;
  padding: 8px 0 8px 22px;
  border-radius: 4px 4px 4px 0;
}

.QuestionGroup-questions {
  position: relative;
  padding-left: 40px;
  padding-top: 36px;
}

.QuestionGroup-border {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  border-radius: 0 0 3px 3px;
  border: 3px solid #111d4a;
  display: inline-block;
}



