.Results {
}

.Results-header {
  width: 724px;
  margin-left: auto;
  margin-right: auto;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
  margin-top: 12px;
}

.Results-title {
  font-size: 32px;
  line-height: 32px;
  background-color: #111d4a;
  color: #f5f5f5;
  padding-top: 12px;
  padding-left: 18px;
  padding-bottom: 12px;
  display: inline-block;
  vertical-align: top;
  border-radius: 0 4px 4px 0;
  width: 650px;
}

.Results-logo-container {
  vertical-align: top;
  display: inline-block;
  position: relative;
  height: 56px;
  width: 56px;
  background: -webkit-gradient(linear, left top, right top, from(#FE4A49), to(#e44241));
  background: -webkit-linear-gradient(left, #FE4A49, #e44241);
  background: -o-linear-gradient(left, #FE4A49, #e44241);
  background: linear-gradient(to right, #FE4A49, #e44241);
  border-radius: 4px 0 0 4px;
  overflow: hidden;
}

.Results-logo-container:hover {
  opacity: 0.8;
}

.Results-logo {
  position: absolute;
  top: -15px;
  left: -14px;
  width: 85px;
  height: 85px;
}

.Results-body {
  width: 700px;
  margin: auto;
}



