.Quiz {
}

.Quiz-header {
  width: 724px;
  margin-left: auto;
  margin-right: auto;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.1);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.1);
  margin-top: 12px;
}

.Quiz-title {
  font-size: 32px;
  line-height: 32px;
  background-color: #BDD9BF;
  padding-top: 12px;
  padding-left: 18px;
  padding-bottom: 12px;
  display: inline-block;
  vertical-align: top;
  border-radius: 0 4px 4px 0;
  width: 650px;
}

.Quiz-logo-container {
  vertical-align: top;
  display: inline-block;
  position: relative;
  height: 56px;
  width: 56px;
  background: -webkit-gradient(linear, left top, right top, from(#2AB7CA), to(#54c5d4));
  background: -webkit-linear-gradient(left, #2AB7CA, #54c5d4);
  background: -o-linear-gradient(left, #2AB7CA, #54c5d4);
  background: linear-gradient(to right, #2AB7CA, #54c5d4);
  border-radius: 4px 0 0 4px;
  overflow: hidden;
}

.Quiz-logo-container:hover {
  opacity: 0.8;
}

.Quiz-logo {
  position: absolute;
  top: -15px;
  left: -14px;
  width: 85px;
  height: 85px;
}

.Quiz-body {
  width: 700px;
  margin: auto;
}

.Quiz-finish {
  text-decoration: none;
  background-color: #FE4A49;
  color: #f5f5f5;
  padding: 8px;
  font-size: 24px;
  display: block;
  width: 300px;
  margin: 48px auto;
  border-radius: 4px;
  text-align: center;
}

.Quiz-finish:hover {
  opacity: 0.8;
}


